import React from "react";
import "../component-css/Signup.css";
import { Link } from "react-router-dom";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";

const initialValues = {
  name: "",
  email: "",
  password: "",
};

const onSubmit = (values) => {
  console.log("Form data", values);
};

const validationSchema = Yup.object({
  name: Yup.string().required("Mandatory Field"),
  email: Yup.string().email("Invalid email format").required("Mandatory Field"),
  password: Yup.string()
    .required("Mandatory Field")
    .min(6, "The length of Password should be 6-50 characters"),
});

function Signup() {
  // console.log("Form errors", formik.errors);

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={onSubmit}
    >
      <div className="register__container">
        <div className="register__title">
          <h3>Create your Lazada Account</h3>
          <div>
            Already member?<Link to="/login">Login</Link> here.
          </div>
        </div>
        <Form className="register__form">
          <div className="register__form__body">
            <div className="register__form__body__col1">
              {/* ///////////////////////////////////////// */}
              <div className="register__phone">
                <label htmlFor="email">Email Address*</label>
                <Field
                  name="email"
                  inputMode="numeric"
                  pattern="[0-9]*"
                  type="email"
                  placeholder="Please enter your email"
                />
                <span>
                  <ErrorMessage name="email">
                    {(errorMsg) => (
                      <span className="warning__span">{errorMsg}</span>
                    )}
                  </ErrorMessage>
                  <span className="warning__span"></span>
                </span>
              </div>
              <div className="phone__sms"></div>
              {/* ///////////////////////////////////////// */}
              <div className="register__password">
                <label htmlFor="password">Password*</label>
                <Field
                  name="password"
                  type="password"
                  placeholder="Minimum 6 characters with a number and a letter"
                />
                <span>
                  <ErrorMessage name="password">
                    {(errorMsg) => (
                      <span className="warning__span">{errorMsg}</span>
                    )}
                  </ErrorMessage>
                  <span className="warning__span"></span>
                </span>
              </div>
              <div className="register__info">
                <div className="register__info__bday">
                  <div className="register__info__bday__title">Birthday</div>
                  <div className="register__info__bday__input">
                    <div className="input__month"></div>
                  </div>
                </div>
                <div className="register__info__gender"></div>
              </div>
            </div>
            <div className="register__form__body__col2">
              {/* ///////////////////////////////////////// */}
              <div className="register__name">
                <label htmlFor="name">Full name*</label>
                <Field name="name" type="text" placeholder="First Last" />
                <span>
                  <ErrorMessage name="name">
                    {(errorMsg) => (
                      <span className="warning__span">{errorMsg}</span>
                    )}
                  </ErrorMessage>
                  <span className="warning__span"></span>
                </span>
              </div>
              <div className="register__promotions">
                <input
                  className="checkbox"
                  type="checkbox"
                  value="on"
                  aria-checked="false"
                />{" "}
                <p>
                  I want to receive exclusive offers and promotions from Lazada.
                </p>
              </div>
              <div className="register__button">
                <button className="sign__logn__in__btn" type="submit">
                  SIGN UP
                </button>
              </div>
              <div className="register__policy">
                <span>
                  By&nbsp;clicking&nbsp;“SIGN&nbsp;UP”,&nbsp;I&nbsp;agree&nbsp;to&nbsp;Lazada's{" "}
                  <Link to="/">Terms&nbsp;of&nbsp;Use </Link>
                </span>
                and <Link to="/">Privacy&nbsp;Policy</Link>
              </div>
              <div className="register__change">
                <p>Or, sign up with</p>
                <div className="register__change__btn">
                  <Link to="/signupPhone">
                    <button className="sign__logn__in__btn" type="button">
                      Sign up with Mobile
                    </button>
                  </Link>
                </div>
              </div>
              <div className="register__third">
                <button className="register__third__fb">Facebook</button>
                <button className="register__third__google">Google</button>
              </div>
            </div>
          </div>
        </Form>
      </div>
    </Formik>
  );
}

export default Signup;
