import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import "../component-css/Login.css";
import { auth } from "./Firebase";

function Login() {
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const login = (event) => {
    event.preventDefault();

    auth
      .signInWithEmailAndPassword(email, password)
      .then((auth) => {
        history.push("/");
      })
      .catch((e) => alert(e.message));
  };

  const register = (event) => {
    event.preventDefault();

    auth
      .createUserWithEmailAndPassword(email, password)
      .then((auth) => {
        history.push("/");
      })
      .catch((e) => alert(e.message));
  };

  return (
    <div className="login">
      <div className="login__title">
        <h3>Welcome to Lazada! Please login.</h3>
        <div>
          New member? <Link to="/signupPhone">Register</Link> here.
        </div>
      </div>
      <form className="login__form">
        <div className="login__form__body">
          <div className="login__input">
            <div className="login__input__name">
              <label className="login__input__label">
                Phone Number or Email
              </label>
              <input
                value={email}
                onChange={(event) => setEmail(event.target.value)}
                className="login__input__input"
                type="email"
                placeholder="Please enter your Phone Number or Email"
              ></input>
              <span className="warning__span"></span>
            </div>
            <div className="login__input__password">
              <label className="login__input__label">Password</label>
              <input
                value={password}
                onChange={(event) => setPassword(event.target.value)}
                className="login__input__input"
                type="password"
                placeholder="Please enter your password"
              ></input>
              <span className="warning__span"></span>
            </div>
            <div className="login__forgot">
              <Link to="/">Forgot Password?</Link>
            </div>
          </div>
          <div className="login__buttons">
            <div className="login__button">
              <button
                className="sign__logn__in__btn"
                onClick={login}
                type="submit"
              >
                LOGIN
              </button>
              <hr />
              <button
                className="sign__logn__in__btn"
                onClick={register}
                type="submit"
              >
                REGISTER
              </button>
            </div>
            <div className="login__third__party">
              <div className="login__third__party__body">
                <div className="login__third__party__line">
                  <span>Or, login with</span>
                </div>
                <div className="login__third__party__buttons">
                  <button className="login__fb">Facebook</button>
                  <button className="login__google">Google</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}

export default Login;
