import React from "react";
import { useStateValue } from "../../StateProvider";
import { Link } from "react-router-dom";

function Flashproducts({ product }) {
  const [{ cart }, dispatch] = useStateValue();

  const addToCart = () => {
    dispatch({
      type: "ADD_TO_Cart",
      item: {
        title: product.title,
        image: product.image,
        price: product.price,
        discount1: product.discount1,
        discount2: product.discount2,
        description: product.description,
        delivery: product.delivery,
        shop: product.shop,
      },
    });
  };
  return (
    <div>
      <Link to="/">
        <div className="product">
          <div className="product__info">
            <img src={product.image} alt="" />
            <p className="product__title">{product.title}</p>
            <p className="product__price">
              ₱
              {product.price.toLocaleString(navigator.language, {
                minimumFractionDigits: 2,
              })}
            </p>
            <p className="product__discount">
              <span>‎{product.discount1}</span>&nbsp;
              <span>{product.discount2}</span>
            </p>
          </div>
        </div>
      </Link>
      <button className="product__button" onClick={addToCart}>
        Add To Cart
      </button>
    </div>
  );
}

export default Flashproducts;
