import "../../component-css/Navcategory.css";
import KeyboardArrowRightOutlinedIcon from "@material-ui/icons/KeyboardArrowRightOutlined";
import React from "react";
import SidenavCategory2 from ".././sub-components/SidenavCategory2";

function Navcategory() {
  return (
    <div className="header__category">
      <div className="header__category__bar">
        <div className="header__category__dropdown">
          Categories
          <KeyboardArrowRightOutlinedIcon className="arrow" />
          <SidenavCategory2 />
        </div>
        <div className="header__category__labels">
          <a href="/" className="channel__1">
            <img
              src="//laz-img-cdn.alicdn.com/images/ims-web/TB18x8whRr0gK0jSZFnXXbRRXXa.png"
              alt=""
            />
            <span>Load &amp; eStore</span>
          </a>
          <a href="/" className="channel__2">
            <img
              src="//laz-img-cdn.alicdn.com/images/ims-web/TB1XMRwhRr0gK0jSZFnXXbRRXXa.png"
              alt=""
            />
            <span>LazMall</span>
          </a>
          <a href="/" className="channel__3">
            <img
              src="//laz-img-cdn.alicdn.com/images/ims-web/TB1x.lwhHj1gK0jSZFuXXcrHpXa.png"
              alt=""
            />
            <span>Vouchers</span>
          </a>
        </div>
      </div>
    </div>
  );
}

export default Navcategory;
