import React from "react";
import "../component-css/Sidenav.css";
import Slideview from "../components/sub-components/Slideview";
import SidenavCategory from "../components/sub-components/SidenavCategory";

function Sidenav() {
  return (
    <div className="nav__category">
      <div className="nav__menu">
        <div className="nav__menu__container">
          <SidenavCategory />
          <Slideview />
        </div>
      </div>
    </div>
  );
}

export default Sidenav;
