import React from "react";
import KeyboardArrowRightOutlinedIcon from "@material-ui/icons/KeyboardArrowRightOutlined";

const SidenavProps = ({ name }) => {
  return (
    <div>
      <span>{name}</span>
      <span>
        <KeyboardArrowRightOutlinedIcon />
      </span>
    </div>
  );
};

export default SidenavProps;
