import React from "react";
import Timer from "../Timeleft";
import { Link } from "react-router-dom";
import FlashList from "./FlashList";

function PageFlashsale() {
  return (
    <div className="page__flash">
      <div className="flash__title">
        <h3>Flash&nbsp;Sale</h3>
      </div>
      <div className="flash__content">
        <div className="flash__content__header">
          <div>
            <div className="flash__content__header__text">
              On&nbsp;Sale&nbsp;Now
            </div>
          </div>
          <div className="flash__content__header__timer">
            <div>Ending In</div>
            <Timer />
          </div>
          <Link to="/" className="flash__content__header__button">
            SHOP&nbsp;ALL&nbsp;PRODUCTS
          </Link>
        </div>
        <div className="flash__content__body">
          <FlashList />
        </div>
      </div>
    </div>
  );
}

export default PageFlashsale;
