import React from "react";
import KeyboardArrowRightOutlinedIcon from "@material-ui/icons/KeyboardArrowRightOutlined";

function Pagechannel() {
  return (
    <div className="page__channels">
      <div className="page__channels__content">
        <div>
          <a href="/" className="channel__1">
            <div>
              <img
                src="//laz-img-cdn.alicdn.com/images/ims-web/TB18x8whRr0gK0jSZFnXXbRRXXa.png"
                alt=""
              />
              <span>Load &amp; eStore</span>
            </div>
            <div>
              <KeyboardArrowRightOutlinedIcon className="channel__icon" />
            </div>
          </a>
        </div>
        <div>
          <a href="/" className="channel__2">
            <div>
              <img
                src="//laz-img-cdn.alicdn.com/images/ims-web/TB1XMRwhRr0gK0jSZFnXXbRRXXa.png"
                alt=""
              />
              <span>LazMall</span>
            </div>
            <div>
              <KeyboardArrowRightOutlinedIcon className="channel__icon" />
            </div>
          </a>
        </div>
        <div>
          <a href="/" className="channel__3">
            <div>
              <img
                src="//laz-img-cdn.alicdn.com/images/ims-web/TB1x.lwhHj1gK0jSZFuXXcrHpXa.png"
                alt=""
              />
              <span>Vouchers</span>
            </div>
            <div>
              <KeyboardArrowRightOutlinedIcon className="channel__icon" />
            </div>
          </a>
        </div>
      </div>
    </div>
  );
}

export default Pagechannel;
