import React from "react";
import "../component-css/Cart.css";
import { useStateValue } from "./StateProvider";
import DeleteForeverTwoToneIcon from "@material-ui/icons/DeleteForeverTwoTone";
import LocationOnOutlinedIcon from "@material-ui/icons/LocationOnOutlined";
import CheckoutProduct from "./sub-components/CheckoutProduct";
import CurrencyFormat from "react-currency-format";
import { getCartTotal } from "./reducer";

function Cart() {
  const [{ cart }] = useStateValue();

  return (
    <div className="cart">
      {cart?.length === 0 ? (
        <div className="empty__cart">
          <div className="empty__cart__text">
            There are no items in this cart
          </div>
          <button className="empty__cart__button">CONTINUE SHOPPING</button>
        </div>
      ) : (
        <div className="cart__container">
          <div className="cart__container__left">
            <div className="cart__container__header">
              <div className="header__container">
                <div className="header__container__main">
                  <label className="header__container__main__checkbox">
                    <input
                      className="checkbox"
                      type="checkbox"
                      aria-checked="false"
                      value="on"
                    ></input>
                    <div>SELECT ALL ({cart?.length} ITEM(S))</div>
                  </label>
                  <div className="header__container__main__operations">
                    <div className="delete__button__container">
                      <span>
                        <DeleteForeverTwoToneIcon />
                      </span>
                      <span>DELETE</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {cart.map((item) => (
              <CheckoutProduct
                title={item.title}
                image={item.image}
                price={item.price}
                discount1={item.discount1}
                discount2={item.discount2}
                description={item.description}
                delivery={item.delivery}
                shop={item.shop}
              />
            ))}
          </div>
          <div className="cart__container__right">
            <div className="summary__section">
              <div className="summary__section__content">
                <div className="checkout__location">
                  <div className="location__label">Location</div>
                  <div className="location__body">
                    <div className="location__icon">
                      <LocationOnOutlinedIcon />
                    </div>
                    <div className="location__address">
                      Metro Manila~Taguig,Taguig City,South Signal Village
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="summary__section">
              <div className="summary__section__heading">Order Summary</div>
              <div
                className="summary__section__content"
                style={{ height: "240px" }}
              >
                <div className="checkout__summary__rows">
                  <div className="checkout__summary__row">
                    <div className="checkout__summary__label">
                      Subtotal ({cart?.length} items)
                    </div>
                    <div className="checkout__summary__value">
                      {" "}
                      <CurrencyFormat
                        decimalScale={2}
                        value={getCartTotal(cart)}
                        displayType={"text"}
                        thousandSeparator={true}
                        prefix={"₱"}
                      />
                    </div>
                  </div>
                  <div className="checkout__summary__row">
                    <div className="checkout__summary__label">Shipping Fee</div>
                    <div className="checkout__summary__value">₱0.00</div>
                  </div>
                </div>
                <div className="voucher__input">
                  <div className="voucher__input__inner">
                    <div className="voucher__input__col">
                      <input
                        type="text"
                        placeholder="Enter Voucher Code"
                      ></input>
                    </div>
                    <div className="voucher__input__button btn">
                      <button type="button">APPLY</button>
                    </div>
                  </div>
                </div>
                <div className="checkout__order__total">
                  <div className="checkout__order__total__row">
                    <div className="checkout__order__total__title">Total</div>
                    <div className="checkout__order__total__fee">
                      <CurrencyFormat
                        decimalScale={2}
                        value={getCartTotal(cart)}
                        displayType={"text"}
                        thousandSeparator={true}
                        prefix={"₱"}
                      />
                      <small>VAT included, where applicable</small>
                    </div>
                  </div>
                  <button>PROCEED TO CHECKOUT</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default Cart;
