import React from "react";
import DeleteForeverTwoToneIcon from "@material-ui/icons/DeleteForeverTwoTone";
import KeyboardArrowRightOutlinedIcon from "@material-ui/icons/KeyboardArrowRightOutlined";
import FavoriteBorderTwoToneIcon from "@material-ui/icons/FavoriteBorderTwoTone";
import Incrementer from "../sub-components/Incrementer";
import { useStateValue } from "../StateProvider";

function CheckoutProduct({
  title,
  image,
  price,
  discount1,
  discount2,
  description,
  delivery,
  shop,
}) {
  const [{ cart }, dispatch] = useStateValue();
  const removeFromCart = () => {
    dispatch({
      type: "REMOVE_FROM_Cart",
      title: title,
    });
  };

  return (
    <div className="cart__container__shop">
      <div className="cart__container__shop__title">
        <div className="checkout__shop">
          <label className="checkout__shop__left">
            <input
              className="checkbox"
              type="checkbox"
              aria-checked="false"
              value="on"
            ></input>
            <div className="checkout__shop__title">
              <span>{shop}</span>
              <KeyboardArrowRightOutlinedIcon />
            </div>
          </label>
          <div className="checkout__shop__right">
            <span>Get Voucher</span>
            <KeyboardArrowRightOutlinedIcon />
          </div>
        </div>
        <div className="checkout__shop__progress"></div>
        <div className="checkout__shop__delivery">
          <div className="checkout__shop__delivery__fee">{delivery}</div>
        </div>
      </div>
      <div className="cart__container__shop_products">
        <div className="cart__item">
          <div className="cart__item__inner">
            <div className="cart__item__left">
              <label>
                <input
                  className="checkbox"
                  type="checkbox"
                  aria-checked="false"
                ></input>
              </label>
              <div className="cart__image">
                <a href="/">
                  <img src={image} alt="" />
                </a>
              </div>
              <div className="cart__content">
                <a href="/">{title}</a>
                <a href="/">{description}</a>
              </div>
            </div>
            <div className="cart__item__middle">
              <p className="cart__item__middle1">
                ₱
                {price.toLocaleString(navigator.language, {
                  minimumFractionDigits: 2,
                })}
              </p>
              <p className="cart__item__middle2">{discount1}</p>
              <p className="cart__item__middle3">{discount2}</p>
              <div className="cart__item__middle__operations">
                <span>
                  <FavoriteBorderTwoToneIcon />
                </span>
                <span onClick={removeFromCart}>
                  <DeleteForeverTwoToneIcon />
                </span>
              </div>
            </div>
            <div className="cart__item__right">
              <div className="item__quantity">
                <div className="item__quantity__picker">
                  <Incrementer />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CheckoutProduct;
