import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "../../component-css/Slideview.css";

export default class Slideview extends React.Component {
  render() {
    var settings = {
      dots: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      infinite: false,
      autoplay: true,
      autoplaySpeed: 5000,
    };
    return (
      <Slider {...settings} className="Slider">
        <div>
          <img
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB1OPsCUlr0gK0jSZFnXXbRRXXa.jpg_1200x1200.jpg"
            alt=""
          />
        </div>
        <div>
          <img
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB1.bYVUlr0gK0jSZFnXXbRRXXa.jpg_2200x2200Q100.jpg_.webp"
            alt=""
          />
        </div>
        <div>
          <img
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB1Rs6VUlr0gK0jSZFnXXbRRXXa.jpg_2200x2200Q100.jpg_.webp"
            alt=""
          />
        </div>
        <div>
          <img
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB1ob3_gCslXu8jSZFuXXXg7FXa.jpg_2200x2200Q100.jpg_.webp"
            alt=""
          />
        </div>
        <div>
          <img
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB123rgj2zO3e4jSZFxXXaP_FXa.jpg_2200x2200Q100.jpg_.webp"
            alt=""
          />
        </div>
        <div>
          <img
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB1Mu9iUBr0gK0jSZFnXXbRRXXa.jpg_2200x2200Q100.jpg_.webp"
            alt=""
          />
        </div>
        <div>
          <img
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB1ZunwgSslXu8jSZFuXXXg7FXa.jpg_2200x2200Q100.jpg_.webp"
            alt=""
          />
        </div>
        <div>
          <img
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB1yxqiUBr0gK0jSZFnXXbRRXXa.jpg_2200x2200Q100.jpg_.webp"
            alt=""
          />
        </div>
      </Slider>
    );
  }
}
